import ObservingDiv from 'components/ObservingDiv';
import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { darcula as codeStyle } from 'react-syntax-highlighter/dist/esm/styles/prism';

import styles from './styles.module.scss'

type Props = {
  url: string | React.ReactNode,
  request: string,
  response: string,
  defaultPreview: React.ReactNode,
  activePreview: React.ReactNode
}


const DemoPreview : React.FC<Props> = ({url,request,response,defaultPreview, activePreview}) => {
  const [opened, setOpened] = React.useState(false)

  return <ObservingDiv className={styles.demoPreview}>
    <div className={styles.browsers}>
      <div className={styles.browser + " " +  styles.request}>
        <div className={styles.browserHeader}>
          <div className={styles.buttons}>
            <div/>
            <div/>
            <div/>
          </div>
          <div className={styles.url}>
            {url}
          </div>
        </div>
        <div className={styles.browserBody}>
          <SyntaxHighlighter language="javascript" style={codeStyle} wrapLongLines={true} useInlineStyles={false}>
            {request}
          </SyntaxHighlighter>
        </div>
      </div>
      {!opened ? <div className={styles.runButton} onClick={()=>setOpened(true)}>
        Run
      </div> : null}
      {opened ? <div className={styles.browser + " " + styles.response}>
        <div className={styles.browserHeader}>
          <div className={styles.buttons}>
            <div onClick={()=>setOpened(false)}/>
          </div>
          <div className={styles.url}>
            RESPONSE
          </div>
        </div>
        <div className={styles.browserBody}>
          <SyntaxHighlighter language="javascript" style={codeStyle}  wrapLongLines={true} useInlineStyles={false}>
            {response}
          </SyntaxHighlighter>
        </div>
      </div> : null}
    </div>
    <div className={styles.preview}>{!opened ? defaultPreview : activePreview}</div>
  </ObservingDiv>
};

DemoPreview.defaultProps = {
  url: "",
  request: "",
  response: "",
  defaultPreview: <div/>,
  activePreview: <div/>
}

export default DemoPreview;