import React from 'react';
import styles from './styles.module.scss'
import ObservingDiv from '../../../../components/ObservingDiv';
import CSS from 'csstype';
type Props = {
  cardID : number | string,
  title: string,
  imageSrc: string,
  description: string,
  animationDelay: CSS.Property.AnimationDelay,
}

const DemoCard : React.FC<Props> = ({cardID, title, imageSrc, description, animationDelay}) => {
  return  <ObservingDiv className={styles.demoCard} style={{animationDelay: animationDelay}}>
    <div className={styles.cardID}>{cardID}</div>
    <img src={imageSrc} alt={imageSrc}/>
    <div className={styles.cardBody}>
      <em>{title}. </em>{description}
    </div>
  </ObservingDiv>
}

DemoCard.defaultProps = {
  cardID: 0,
  title: "",
  imageSrc: "",
  description: ""
}

export default DemoCard;
