import React from 'react';
import styles from './styles.module.scss';

type Props = {
  selected: number
}

const LinkData = [
  {
    href : "/",
    name : "Home"
  },
  {
    href : "/about",
    name : "About"
  },
  {
    href : "mailto:dev@twelvelabs.io",
    name : "Contact Us"
  }
]

const Header : React.FC<Props> = ({selected}) => {
  return <div className={styles.root}>
    <div>
      <a href="/">
        <img src="/logo192.png" alt="logo"/>
      </a>
    </div>
    <div className={styles.anchors}>
      {LinkData.map(({href, name}, idx) => {
        return <a href={href} key={href} className={idx === selected ? styles.selected : ""}>
          {name}
        </a>
      })}
    </div>
  </div>
}

export default Header