import React from 'react';
import styles from './styles.module.scss'

type Props = {
  tabData: string[],
  callback: (tabID : number) => void
}

const PreviewTab : React.FC<Props> = ({tabData, callback}) => {
  const [tabID, setTabID] = React.useState(0);
  const [selectionStyle, setSelectionStyle] = React.useState({left: 0, width: 120});
  const selfRef = React.useRef(null);
  const onTabClick = React.useCallback((idx)=>{
    callback(idx);
    setTabID(idx);
    const child = (selfRef.current as any).children[idx + 1];
    setSelectionStyle({
      left: child.offsetLeft,
      width:  child.offsetWidth,
    });
  }, [selfRef, setTabID, callback,setSelectionStyle])

  React.useEffect(() => {
    setTimeout(() => onTabClick(0), 100);
  },[onTabClick]);

  React.useEffect(() => {
    //setTimeout(() => onTabClick(0), 100);

    const onResize = () => {
      const child = (selfRef.current as any).children[tabID + 1];
      if (selectionStyle.left === child.offsetLeft && selectionStyle.width === child.offsetWidth ) {
        return;
      }
      setSelectionStyle({
        left: child.offsetLeft,
        width:  child.offsetWidth,
      });
    }
    window.addEventListener("resize", onResize);
    return () => {
      window.removeEventListener('resize', onResize);
    };
  },[tabID, onTabClick, setSelectionStyle, selectionStyle]);

  return  <div className={styles.previewTab} ref={selfRef}>
    <div className={styles.selection} style={selectionStyle}></div>
    {
      tabData.map((title, idx) => {
        return <div key={idx} className={idx===tabID ? styles.selectedTab : ""} onClick={()=>onTabClick(idx)}>
          <span>{title}</span>
        </div>
      })
    }
  </div>
}

export default PreviewTab;
