import React from 'react';
import styles from './styles.module.scss';
import { ReactComponent as CheckBox } from './check.svg';
import CSS from 'csstype'
import ObservingDiv from '../../../../components/ObservingDiv';
type Props = {
  imageSrc: string,
  title: string,
  items: string[],
  delay: CSS.Property.AnimationDelay
};

const DataColumn: React.FC<Props> = ({imageSrc, title, items, delay} : Props) => {
  return (
    <ObservingDiv style={{animationDelay: delay}} className={styles.column}>
      <img className={styles.image} src={imageSrc} alt={imageSrc}/>
      <div className={styles.title}>{title}</div>
      <div className={styles.items}>
        {items.map((item)=>{
          return <div key={item}><CheckBox/><span>{item}</span></div>
        })}
      </div>
    </ObservingDiv>
  );
}

export default DataColumn;
