import React from 'react';
import styles from './styles.module.scss';
import CSS from 'csstype';
import ObservingDiv from '../../../../components/ObservingDiv';
type Props = {
  title: string,
  description: string,
  imageUrl: string,
  delay: CSS.Property.AnimationDelay,
}

const Reason : React.FC<Props> = ({title, description, imageUrl, delay}) => {
  return <ObservingDiv style={{animationDelay: delay}} className={styles.reason}>
    <img src={imageUrl} alt={imageUrl}/>
    <div className={styles.text}>
      <div className={styles.title}>
        {title}
      </div>
      <div className={styles.description}>
        {description}
      </div>
    </div>
  </ObservingDiv>
}

Reason.defaultProps = {
  title : "",
  description : ""
}

export default Reason;